<template>
	<div class="build">
		<en-search @vague='entitle' :title="serach" @company="companyFn"></en-search>
		<div class="total">
			共找到<span>{{total}}</span>条在建信息(仅查询湖南省在建信息)
		</div>
		<div class="build-list">
			<!-- 判断是否加载中 -->
			<template v-if="isajax">
				<!-- 有数据 -->
				<template v-if="list&&list.length>0">
					<table class="table-content">
						<tr>
							<td style="width:84px">序号</td>
							<td style="width:108px">姓名</td>
							<td style="width:296px">单位名称</td>
							<td style="width:calc(100% - 704px)">工程名称</td>
							<td style="width:216px">岗位类别</td>
						</tr>
						<tr v-for='(el,i) in list' :key='i' @click='tobuild(el)'>
							<td>{{(data.pageNo-1)*20+(i+1)}}</td>
							<td>{{el.name}}</td>
							<td>{{el.unitOrg}}</td>
							<td>{{el.proName}}</td>
							<td>{{el.type}}</td>
						</tr>
					</table>
					<div class="page">
						<nav-page :all='total' :currents='data.pageNo' @skip='Goto'></nav-page>
					</div>
				</template>
				<!-- 无数据  -->
				<template v-else-if="list&&list.length==0">
					<div class="no-toast">
						<img src="../../assets/img/bank_card @2x.png" alt="">
						<span>Sorry，没有找到相关在建信息</span>
					</div>
				</template>
				<!-- 加载失败 -->
				<template v-else-if="!list">
					<div class="ajax-erroe">
						<img src="../../assets/img/pic-zoudiu.png" />
						<span @click="recoldFn">刷新</span>
					</div>
				</template>
			</template>
			<template v-else>
				<div style="min-height:240px" v-loading="loading" element-loading-text="拼命加载中"></div>
			</template>


		</div>
		<f-vip @toChildEvent='closeload' v-if='svip'></f-vip>
	</div>
</template>
<script>
	import {
		under,
		underq
	} from '@/api/index'
	export default {
		data() {
			return {
				// name:'',
				// idcard:'',
				total: 0,
				list: [],
				// noList:false,
				// title:'',
				svip: false,
				serach: '',
				data: {
					name: '',
					pageNo: 1,
					pageSize: '20',
					searchType: 0,
					idCard: ''
				},
				isajax: false,
				loading: true,
			}
		},
		inject: ['reload'],
		created() {
			this.serach = localStorage.getItem('title') ? localStorage.getItem('title') : ''
			//如果是刷新操作，则复现上次
			if (sessionStorage.getItem('zjSerach')) {
				let data = JSON.parse(sessionStorage.getItem('zjSerach'));
				this.data = data;
				this.serach = data.name;
				if (this.data.idCard != '') {
					this.serach = this.data.idCard;
				}
			}
			if (this.data.idCard != '') {
				let that = this;
				let idcard = this.data.idCard.replace(/x/g, 'X');
				underq({
					name: 'aaaa',
					idCard: idcard,
					type: 'api'
				}).then(res => {
					that.isajax = true;
					if (res.code == 1) {
						if (res.data.length == 0) {
							this.$confirm('该人员无在建信息', '提示', {
								showCancelButton: false,
								showConfirmButton: false,
								type: 'warning'
							})
						} else {
							that.list = res.data;
							that.total = res.data.length;
							// const { href } = this.$router.resolve({
							//   path:'/certifi',query:{card:idcard} 
							// })
							//   window.open(href, '_blank', )
						}
					} else {
						this.$confirm(res.msg, '提示', {
							showCancelButton: false,
							showConfirmButton: false,
							type: 'warning'
						})
					}
				})
			} else {
				this.gainList(this.serach)
			}

		},
		methods: {
			entitle(val) {
				if (localStorage.getItem('0658544ac523fca9ec78a5f607fdd7ee')=='false') {
					this.svip = true
					this.modalHelper.afterOpen();
					return
				}
				let that = this;
				this.serach = val.cur;
				let reNumber = /^\d+$/;
				if (reNumber.test(this.serach[0])) { //判断第一位是否为数字,则进身份证
					let sfz =
						/^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/;
					if (!sfz.test(this.serach)) {
						this.$confirm('请输入正确的身份证号', '提示', {
							showCancelButton: false,
							showConfirmButton: false,
							type: 'warning'
						})
						return
					}
					this.isajax = false;
					this.data.idCard = this.serach;
					let idcard = this.serach.replace(/x/g, 'X');
					underq({
						name: 'aaaa',
						idCard: idcard,
						type: 'api'
					}).then(res => {
						this.isajax = true;
						if (res.code == 1) {
							if (res.data.length == 0) {
								this.$confirm('该人员无在建信息', '提示', {
									showCancelButton: false,
									showConfirmButton: false,
									type: 'warning'
								})
							} else {
								that.list = res.data;
								that.total = res.data.length;
								const {
									href
								} = this.$router.resolve({
									path: '/certifi',
									query: {
										card: idcard
									}
								})
								window.open(href, '_blank', )
							}
						} else {
							this.$confirm(res.msg, '提示', {
								showCancelButton: false,
								showConfirmButton: false,
								type: 'warning'
							})
						}
					})
				} else {
					this.isajax = false;
					this.list = [];
					this.data.pageNo = 1;
					this.gainList(this.serach);
				}

			},
			closeload(val) {
				this.svip = val.cur
			},
			tobuild(el) {
				if (sessionStorage.getItem('xtoken') || localStorage.getItem('Xtoken')) {
					if (localStorage.getItem('0658544ac523fca9ec78a5f607fdd7ee')=='false') {
						this.svip = true
						this.modalHelper.afterOpen();
					} else {
						const {
							href
						} = this.$router.resolve({
							path: '/certifi',
							query: {
								id: el.pkid
							}
						})
						window.open(href, '_blank', )
					}
				} else {
					this.$confirm(this.qjTipTxt, '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						this.$router.push('/logo')
					})
				}
			},
			gainList(name = null) {
				let that = this;
				let data = this.data;
				if (this.data.searchType == 1) {
					data.name = this.serach;
				}
				if (name != null) {
					data.name = name;
					this.serach = name;
				}
				under(data).then(res => {
					this.isajax = true;
					if (res.code == 1) {
						this.total = res.total
						this.list = res.data
					}
				}).catch(function(res) {
					that.isajax = true;
					that.list = null;
				})
			},
			Goto(val) {
				this.isajax = false;
				this.list = [];
				this.data.pageNo = val.cur;
				this.funcom.toList(205)
				this.gainList()
			},
			companyFn(val) { //企业搜索
				if (localStorage.getItem('0658544ac523fca9ec78a5f607fdd7ee')=='false') {
					this.svip = true
					this.modalHelper.afterOpen();
					return
				}
				this.list = [];
				this.data.searchType = 1;
				this.serach = val.cur;
				this.data.pageNo = 1
				this.isajax = false;
				this.gainList()
			},
			//刷新
			recoldFn() {
				this.reload();
			}
		},
		watch: {
			data: { //监听data变化，并实时保存
				deep: true,
				handler(val, old) {
					sessionStorage.setItem('zjSerach', JSON.stringify(val));
				}
			}
		},
		beforeDestroy() {
			sessionStorage.removeItem('zjSerach')
		},
	}
</script>
<style lang="less" scoped>
.build {
}
</style>



